Favorites = new Mongo.Collection('favorites');

if (Meteor.isServer) {
    Meteor.methods({
        addNewFavorite: async function (data) {
            check(Meteor.userId(), String);
            check(data, {
                id_record: Match.Maybe(String),
                name: String,
                active: Boolean,
            });

            let user = Meteor.user();
            let favorite = _.extend(data, {
                id_owner: user._id,
                date_create: new Date().getTime(),
            });
            delete favorite.id_record;
            return await Favorites.insert(favorite);
        },
        updateFavorite: async function (data) {
            check(Meteor.userId(), String);
            check(data, {
                id_record: String,
                name: String,
                active: Boolean,
            });
            let _id = data.id_record;
            let favorite = _.extend(data, {
                date_update: new Date().getTime(),
            });
            delete favorite.id_record;
            return await  Favorites.update(_id, {
                $set: favorite,
            });

        },
        removeFavorite: async function (_id) {
            check(Meteor.userId(), String);
            check(_id, String);
            return await Favorites.remove({_id: _id});
        }
    });
}
