import {Meteor} from 'meteor/meteor'

Branchs = new Mongo.Collection('branchs');

if (Meteor.isServer) {
    Meteor.methods({
        addNewBranch: async function (data) {
            check(Meteor.userId(), String);
            check(data, {
                id_record: Match.Maybe(String),
                name: String,
                active: Boolean
            });

            let user = Meteor.user();
            let branch = _.extend(data, {
                id_owner: user._id,
                date_create: new Date().getTime(),
            });
            delete branch.id_record;
            return await Branchs.insert(branch);
        },
        updateBranch: async function (data) {
            check(Meteor.userId(), String);
            check(data, {
                id_record: String,
                name: String,
                active: Boolean
            });
            let _id = data.id_record;
            let branch = _.extend(data, {
                date_update: new Date().getTime(),
            });
            delete branch.id_record;
            return await  Branchs.update(_id, {
                $set: branch,
            });

        },
        removeBranch: async function (_id) {
            check(Meteor.userId(), String);
            check(_id, String);
            /* check term using other document */
            let ids = [_id];
            let branch = await Documents.findOne({
                branch: {
                    $elemMatch: {
                        "$in": ids
                    }
                }
            });

            if(branch){
                return null;
            }

            if (Branchs.find(_id).count()) {
                return await Branchs.remove({_id: _id});
            } else {
                return null;
            }
        }
    });
}


