Queues = new Mongo.Collection('queues');

if (Meteor.isServer) {
    Meteor.methods({
        'addQueue': async function (idDoc) {
            check(Meteor.userId(), String);
            check(idDoc, String);
            let user = Meteor.user();
            if (Documents.findOne(idDoc) && !Queues.findOne({id_doc: idDoc, id_owner: user._id})) {
                return await Queues.insert({
                    id_owner: user._id,
                    id_doc: idDoc,
                    date_create: new Date().getTime(),
                });
            }
        },
        'removeQueue': async function (idDoc) {
            check(Meteor.userId(), String);
            check(idDoc, String);
            let user = Meteor.user();
            let queue = Queues.findOne({id_doc: idDoc, id_owner: user._id});
            if (queue) {
                Queues.remove(queue._id);
            }
        },
        "cleanQueue": function () {
            "use strict";
            check(Meteor.userId(), String);
            Queues.remove({id_owner: Meteor.userId()});
        }
    });
}
