Meteor.publish('documents', function () {
    return Documents.find({});
});

Meteor.publish('document', function (idDoc) {
    return Documents.find(idDoc);
})

Meteor.publish('documents.in.fav', function (idFav) {
    let allIdinFav = IdDocumentInFavorite.find({idFav: idFav, id_owner: Meteor.userId()}).fetch().map((val, idx) => {
        "use strict";
        return val.idDoc;
    });
    return Documents.find({
        _id: {
            $in: allIdinFav
        }
    });
});
