import {Meteor} from 'meteor/meteor';
import * as ServiceRemind from '../../imports/server-remind-func'

let sixHours = 1000 * 60 * 60 * 6;
Meteor.startup(() => {
    ServiceRemind.saveRemind();
    ServiceRemind.deleteOldZip();
    setInterval(function () {
        ServiceRemind.saveRemind();
        ServiceRemind.deleteOldZip();
    }, sixHours);
});
