async function deleteEmailValid() {
	const acc  = await Accounts.findUserByEmail(GUEST_QUEUE);
	if(acc && acc._id){
		Accounts.removeEmail(acc._id, GUEST_QUEUE);
		Accounts.setUsername(acc._id, Random.id());
	}
}
