var requireLogin = function () {
    if (!Meteor.user()) {
        if (Meteor.loggingIn()) {
            this.render(this.loadingTemplate);
        } else {
            this.render('deny');
        }
    } else {
        this.next();
    }
}

Router.configure({
    layoutTemplate: 'layout',
    loadingTemplate: 'loading',
    notFoundTemplate: 'notfound',
    waitOn: function () {
        return [
            Meteor.subscribe('queues'),
            Meteor.subscribe('documents.in.queue'),
            Meteor.subscribe('categorys'),
            Meteor.subscribe('branchs'),
            Meteor.subscribe('terms'),
            Meteor.subscribe('documentremind'),
            Meteor.subscribe('documentsinremind'),
            Meteor.subscribe('favorites'),
            Meteor.subscribe('document.in.favorite'),
            Meteor.subscribe('hashtags'),
        ]
    }
});

Router.route('/', {
    name: 'dashboard',
    onBeforeAction: requireLogin,
});

Router.route('/categorys', {
    name: 'categorys',
    onBeforeAction: requireLogin,
});

Router.route('/branchs', {
    name: 'branchs',
    onBeforeAction: requireLogin,
});

Router.route('/terms', {
    name: 'terms',
    onBeforeAction: requireLogin,
});

Router.route('/favorites', {
    name: 'favorites',
    onBeforeAction: requireLogin,
});

Router.route('/reminds', {
    name: 'reminds',
    onBeforeAction: requireLogin,
    waitOn: function () {
        return [
            Meteor.subscribe('reminds')
        ]
    }
});

Router.route('/hashtags', {
    name: 'hashtags',
    onBeforeAction: requireLogin,
});

Router.route('/all-favs', {
    name: 'allfavs',
    onBeforeAction: requireLogin,
});


Router.route('/queues', {
    name: 'printqueues',
    onBeforeAction: requireLogin,
});


Router.route('/docremind', {
    name: 'documentremind',
    onBeforeAction: requireLogin,
});


Router.route('/comming-soon', {
    name: 'commingsoon',
    onBeforeAction: requireLogin,
});


Router.route('/edit-fav/:_idFav', {
    name: 'editfav',
    onBeforeAction: requireLogin,
    waitOn: function () {
        return [
            Meteor.subscribe('documents.in.fav', this.params._idFav),
        ]
    },
    data: function () {
        return {
            idFav: this.params._idFav,
        }
    },
});


Router.route('/all-documents/:_uuid', {
    name: 'alldocuments',
    onBeforeAction: requireLogin,
    waitOn: function () {
        return [
            Meteor.subscribe('rssearchs', this.params._uuid),
        ]
    },
    data: function () {
        return {
            uuid: this.params._uuid,
        }
    },
    action: function () {
        let cloneUUID = Random.id();
        let paramUUID = this.params._uuid;
        if (!paramUUID || paramUUID.trim().length != cloneUUID.length) {
            this.redirect('/all-documents/' + cloneUUID);
        } else {
            this.render();
        }

    }
});

Router.route('/add-documents/:_uuid', {
    name: 'adddocuments',
    onBeforeAction: requireLogin,
    waitOn: function () {
        return [
            Meteor.subscribe('items', this.params._uuid),
            Meteor.subscribe('uploads', this.params._uuid),
        ]
    },
    data: function () {
        return {
            item: Items.findOne(),
            uploads: Uploads.find(),
            uuid: this.params._uuid,
        }
    },
    action: function () {
        let cloneUUID = Random.id();
        let paramUUID = this.params._uuid;
        if (!paramUUID || paramUUID.trim().length != cloneUUID.length) {
            this.redirect('/add-documents/' + cloneUUID);
        } else {
            this.render();
        }

    }
});

