Router.route('/edit-document/:_idDoc/:_uuid', {
    name: 'editdoc',
    onBeforeAction: requireLogin,
    waitOn: function () {
        return [
            Meteor.subscribe('items', this.params._uuid),
            Meteor.subscribe('uploads', this.params._uuid),
            Meteor.subscribe('document', this.params._idDoc),
            Meteor.subscribe('attachment', this.params._idDoc),
        ]
    },
    data: function () {
        return {
            item: Items.find(),
            uploads: Attachments.find({idDocument: this.params._idDoc}),
            uuid: this.params._uuid,
            idDoc: this.params._idDoc,
            doc: Documents.findOne(this.params._idDoc)
        }
    },
    action: function () {
        let idDoc = this.params._idDoc;
        let cloneUUID = Random.id();
        let paramUUID = this.params._uuid;
        if (!paramUUID || paramUUID.trim().length != cloneUUID.length) {
            this.redirect('/edit-document/' + idDoc + '/' + cloneUUID);
        } else {
            this.render();
        }

    }
});
