Reminds = new Mongo.Collection('reminds');

if (Meteor.isServer) {
    Meteor.methods({
        addNewRemind: async function (data) {
            check(Meteor.userId(), String);
            check(data, {
                id_record: Match.Maybe(String),
                name: String,
                active: Boolean,
                interval: Number,
            });

            let user = Meteor.user();
            let remind = _.extend(data, {
                id_owner: user._id,
                date_create: new Date().getTime(),
            });
            delete remind.id_record;
            return await Reminds.insert(remind);
        },
        updateRemind: async function (data) {
            check(Meteor.userId(), String);
            check(data, {
                id_record: String,
                name: String,
                active: Boolean,
                interval: Number,
            });
            let _id = data.id_record;
            let remind = _.extend(data, {
                date_update: new Date().getTime(),
            });
            delete remind.id_record;
            return await  Reminds.update(_id, {
                $set: remind,
            });

        },
        removeRemind: async function (_id) {
            check(Meteor.userId(), String);
            check(_id, String);
            if (Reminds.find(_id).count()) {
                return await Reminds.remove({_id: _id});
            } else {
                return null;
            }
        }
    });

}


