import {Meteor} from 'meteor/meteor'

Categorys = new Mongo.Collection('categorys');

if (Meteor.isServer) {
    Meteor.methods({
        addNewCategory: async function (data) {
            check(Meteor.userId(), String);
            check(data, {
                id_record: Match.Maybe(String),
                name: String,
                active: Boolean
            });

            let user = Meteor.user();
            let category = _.extend(data, {
                id_owner: user._id,
                date_create: new Date().getTime(),
            });
            delete category.id_record;
            return await Categorys.insert(category);
        },
        updateCategory: async function (data) {
            check(Meteor.userId(), String);
            check(data, {
                id_record: String,
                name: String,
                active: Boolean
            });
            let _id = data.id_record;
            let category = _.extend(data, {
                date_update: new Date().getTime(),
            });
            delete category.id_record;
            return await  Categorys.update(_id, {
                $set: category,
            });

        },
        removeCategory: async function (_id) {
            check(Meteor.userId(), String);
            check(_id, String);
            //check document using category
            let document = await Documents.findOne({category: _id});
            if(document){
                return null;
            }
            if (Categorys.find(_id).count()) {
                return await Categorys.remove({_id: _id});
            } else {
                return null;
            }
        }
    });
}



