import {Meteor} from 'meteor/meteor'

Hashtags = new Mongo.Collection('hashtags');

if (Meteor.isServer) {
    Meteor.methods({
        addNewHashtag: async function (data) {
            check(Meteor.userId(), String);
            check(data, {
                id_record: Match.Maybe(String),
                name: String,
                active: Boolean
            });

            let user = Meteor.user();
            let hashtag = _.extend(data, {
                id_owner: user._id,
                date_create: new Date().getTime(),
            });
            delete hashtag.id_record;
            return await Hashtags.insert(hashtag);
        },
        updateHashtag: async function (data) {
            check(Meteor.userId(), String);
            check(data, {
                id_record: String,
                name: String,
                active: Boolean
            });
            let _id = data.id_record;
            let hashtag = _.extend(data, {
                date_update: new Date().getTime(),
            });
            delete hashtag.id_record;
            return await  Hashtags.update(_id, {
                $set: hashtag,
            });

        },
        removeHashtag: async function (_id) {
            check(Meteor.userId(), String);
            check(_id, String);
            /* check term using other document */
            let ids = [_id];
            let hashtag = await Documents.findOne({
                hashtag: {
                    $elemMatch: {
                        "$in": ids
                    }
                }
            });

            if(hashtag){
                return null;
            }

            if (Hashtags.find(_id).count()) {
                return await Hashtags.remove({_id: _id});
            } else {
                return null;
            }
        }
    });
}
