import * as ServiceRemind from '../../imports/server-remind-func'

Documents.find({}).observeChanges({
    added: function (id, fields) {
        ServiceRemind.saveRemind();
    },
    changed: function (id, fields) {
        ServiceRemind.saveRemind();
    },
    removed: function (id) {
        ServiceRemind.saveRemind();
    }
});
