Terms = new Mongo.Collection('terms');

if (Meteor.isServer) {
    Meteor.methods({
        addNewTerm: async function (data) {
            check(Meteor.userId(), String);
            check(data, {
                id_record: Match.Maybe(String),
                name: String,
                active: Boolean,
            });

            let user = Meteor.user();
            let term = _.extend(data, {
                id_owner: user._id,
                date_create: new Date().getTime(),
            });
            delete term.id_record;
            return await Terms.insert(term);
        },
        updateTerm: async function (data) {
            check(Meteor.userId(), String);
            check(data, {
                id_record: String,
                name: String,
                active: Boolean,
            });
            let _id = data.id_record;
            let term = _.extend(data, {
                date_update: new Date().getTime(),
            });
            delete term.id_record;
            return await  Terms.update(_id, {
                $set: term,
            });

        },
        removeTerm: async function (_id) {
            check(Meteor.userId(), String);
            check(_id, String);
            if (Terms.find(_id).count()) {
                /* check term using other document */
                let ids = [_id];
                let document = await Documents.findOne({
                    term: {
                        $elemMatch: {
                            "$in": ids
                        }
                    }
                });

                if (!document) {
                    return await Terms.remove({_id: _id});
                }
            }
            return null;
        }
    });
}



